.profile__name{
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  margin: 0 18px 0 0;
  max-width: 300px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
@media screen and (max-width:768px){
  .profile__name{
    font-size: 27px;
    line-height: 33px;
    margin: 0 10px 0 0;
  }
}
@media screen and (max-width:425px){
  .profile__name{
    max-width: 200px;
  }
}
